.enjoy {
    margin-bottom: 100px;
}

.enjoy_wrapper {
    padding: 100px;
    width: 100%;
    height: 644px;
    background: url('../../../img/enjoy-bg.jpg') center/cover no-repeat;
    border-radius: 40px;
}

.enjoy_inner {
    display: flex;
    flex-direction: column;
    max-width: 530px;
    width: 100%;
    gap: 40px
}

.enjoy_title {
    @include heading-1;
    color: $text-light;
}

.enjoy_word {
    @include heading-1-accent;
    color: $text-accent;
}

.enjoy_description {
    @include body-medium;
    color: $text-light;
}

.enjoy_btn {
    display: flex;
    width: 200px;
    padding: 20px 0;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    background-color: $bg-body;
    @include action-link-button;
    gap: 8px;

    &_text {
        transform: translateX(14px);
        transition: all 0.5s ease-in-out;
    }

    &_logo {
        width: 20px;
        height: 20px;
        opacity: 0;
        transition: all 0.4s linear;
    }

    &:hover &_text {
        transform: translateX(0);
    }

    &:hover &_logo {
        width: 20px;
        opacity: 1;
    }
}