// $text-white : #a5a5a5;
$text-dark : #403F3D;
$text-light : #E1D4C9;
$text-accent : #B0907A;
$bg-body : #E1D4C9;
$bg-container : #665F55;
$bg-backdrop : rgba(64, 63, 61, 0.80);
$border-light : #C1B6AD;
$border-dark : #665F55;
@mixin mainFont($fontSize) {
    font-size: $fontSize;
    font-weight: 400;
}

@mixin heading-1() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 72px;
    font-style: normal;
    font-weight: 600;
    line-height: 105%;
}

@mixin heading-1-accent() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 72px;
    font-style: italic;
    font-weight: 600;
    line-height: 105%;
}

@mixin heading-2() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 60px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
}

@mixin heading-2-accent() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 60px;
    font-style: italic;
    font-weight: 600;
    line-height: 125%;
}

@mixin heading-3() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 24px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
}

@mixin body-medium() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
}

@mixin body-caption() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
}

@mixin action-link-button() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: 150%;
}

@mixin action-burger-link() {
    color: $text-dark;
    font-feature-settings: 'clig' off, 'liga' off;
    font-family: Inter;
    font-size: 32px;
    font-style: normal;
    font-weight: 600;
    line-height: 125%;
}

